import React from "react";
import Card from "react-bootstrap/Card";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
          Привет! Я занимаюсь фронтендом с 2019-го года.
          Мои навыки:
          — Семантическая, кроссбраузерная и адаптивная верстка.
          — JavaScript (ES6+)
          — TypeScript
          — Vue 2/3 + Vuex (Pinia)
          — Webpack
          — Git
          — оценка и декомпозирование задач;
          — Английский язык: pre-intermediate. Могу читать и вести переписку и поддерживать диалог на английском языке. Опыт общения с иностранными клиентами;
          — Опыт разработки коммерческих продуктов;
          — Работа с легаси, разбор и чтение чужого кода, рефакторинг;
          </p>

        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
